h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 1.5rem 0;
  font-weight: 900;
  color: rgb(33, 192, 192);
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  margin: 0;
}

h4 {
  font-size: calc(0.5em + 0.8vw);
  margin: 0;
  padding: 0;
}

h5 {
  font-size: calc(0.5em + 0.8vw);
  font-family: "Poppins", sans-serif;
  color: rgb(33, 192, 192);
  margin: 0;
  padding: 0;
}

p {
  color: #1a1b15;
  font-size: calc(0.4em + 0.8vw);
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  background-color: #FFFFFF;
  color: rgb(33, 192, 192);
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  height: 100%;
  margin: 0;
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

footer {
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4rem;
  position: absolute;
}

hr {
  border: 1px solid #e6e7e1;
  width: 95%;
}

/** Media Queries **/
.mostrar {
  display: block !important;
}

.ocultar {
  display: none !important;
}

.sombraCaja {
  -webkit-box-shadow: 10px 10px 5px -6px rgb(158, 156, 158);
  -moz-box-shadow: 10px 10px 5px -6px rgb(158, 156, 158);
  box-shadow: 10px 10px 5px -6px rgb(158, 156, 158);
}

.sombraTarjetas {
  box-shadow: 0px 0px 10px -2px rgba(148, 139, 139, 0.76);
  -webkit-box-shadow: 0px 0px 10px -2px rgba(148, 139, 139, 0.76);
  -moz-box-shadow: 0px 0px 10px -2px rgba(148, 139, 139, 0.76);
}

.contenido_centrado {
  max-width: 100rem;
  text-align: center;
}

.alinear_derecha {
  display: flex;
  justify-content: flex-end;
}

.alinear_izquierda {
  display: flex;
  justify-content: flex-start;
}

.w-100 {
  width: 100%;
}

.boton_verde {
  background-color: #00e676;
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2rem;
  text-align: center;
  margin: 0.2rem 0;
  padding: 1rem 2rem;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  width: 100%;
}
@media (min-width: 768px) {
  .boton_verde {
    width: auto;
  }
}
.boton_verde:hover {
  background-color: #00b35c;
  cursor: pointer;
}

.boton_rojo {
  background-color: #cb0000;
  display: inline-block;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2rem;
  text-align: center;
  margin: 0.2rem 0;
  padding: 1rem 2rem;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  width: 100%;
}
@media (min-width: 768px) {
  .boton_rojo {
    width: auto;
  }
}
.boton_rojo:hover {
  background-color: #980000;
  cursor: pointer;
}

.btn-leyenda {
  background-color: #e6e6e6;
  display: block;
  color: #FFFFFF;
  font-size: 1.2rem;
  text-align: center;
  margin: 0.5rem 0.5rem 0.5rem 1rem;
  padding: 0.5rem 1rem;
  border: 1px solid #cdcdcd;
  border-radius: 0.5rem;
  transition-property: background-color;
  transition-duration: 0.5s;
}
.btn-leyenda:hover {
  background-color: #cdcdcd;
  cursor: pointer;
}

.btn_pulsado {
  background-color: #00e676;
  display: block;
  color: #FFFFFF;
  font-size: 1.2rem;
  text-align: center;
  margin: 0.2rem 0.2rem 0.2rem 0;
  padding: 1rem 2rem;
  border: none;
  border-radius: 0.5rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  width: 100%;
}
.btn_pulsado:hover {
  background-color: #980000;
  cursor: pointer;
}

.pulsacion {
  cursor: pointer;
}

.pulsacion:active {
  transform: scale(0.8);
}

.boton_verde:active {
  transform: scale(0.9);
  box-shadow: 2px 2px 10px 1px rgba(0, 1, 14, 0.24);
}

